import React, { Component } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import ListSubheader from '@material-ui/core/ListSubheader';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Collapse from '@material-ui/core/Collapse';
import DashboardIcon from '@material-ui/icons/Dashboard';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import StarBorder from '@material-ui/icons/StarBorder';
import { GroupeURL,NewGroupeURL } from './dll/ajax';
import Scroll from './../Progress';
import { TextField } from '@material-ui/core';
import './../index.css'
import { Route, NavLink } from 'react-router-dom';
import { MicNone } from '@material-ui/icons';
import Dialog from './proupe/newGrope';
import Button from '@material-ui/core/Button';
class MainListItems extends Component {

  state = {
    open: true,
    groupe: [],
    scroll:true,
    dialog:false,
    newState:null
  };
  Change(e) {
    let newState = e.target.value;
    this.setState({newState});
  }
  componentDidMount() {

    GroupeURL().then((item) => {
      let groupe = item.data._embedded.groupModelList;
      this.setState({ groupe });
      this.setState({ scroll:false });
       
    });

  }
  render() {
    const handleClick = () => {
      let open = !this.state.open;
      this.setState({ open });
    };
    const newGroupe = () => {
      
      let openNew = !this.state.openNew;
      this.setState({ openNew });
    };
    

    function sendToURL() {
      let name = this.state.newState;
  
      NewGroupeURL({name:name});
      GroupeURL().then((item) => {
        let groupe = item.data._embedded.groupModelList;
        this.setState({ groupe });
        this.setState({ scroll:false });
         
      });
       
  }
  let styles = {
    overflow: 'auto',
    maxHeight:'600px'
  }
    if(this.state.scroll){
      return(<Scroll/>)
    }else{
      return (
      <div>
          
        <ListItem button onClick={newGroupe}>
          <ListItemIcon>
            <DashboardIcon />
          </ListItemIcon>
          <ListItemText primary="Создать группу" />
        </ListItem>

        <Collapse in={this.state.openNew} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
          <ListItemText>
              <p><TextField label="Название группы" style={{marginLeft:'10px'}} onChange={this.Change.bind(this)} defaultValue={""} /></p>
              <p><Button autoFocus onClick={sendToURL.bind(this)} color="primary">
                            Создать
                    </Button></p>
                    </ListItemText>
          </List>
        </Collapse>

        <ListItem button onClick={handleClick}>
          <ListItemIcon>
            <DashboardIcon />
          </ListItemIcon>
          <ListItemText primary="Группы" />
        </ListItem>
       
        <Collapse in={this.state.open} timeout="auto" style={styles} unmountOnExit>
          <List component="div" disablePadding>
            { this.state.groupe.map((body,key)=>(  
                    <NavLink to={{pathname: "/gproupe/"+body.id,state: {data:body._links.lessons.href, id:body.id }}} className="link"  >
                      <ListItem button key={key}>
                        <ListItemIcon>
                          <StarBorder />
                        </ListItemIcon>
                            <ListItemText primary={body.name}/>
                      </ListItem>
                  </NavLink>
                  ))}
          </List>
        </Collapse>
        
        
      </div>
    );
    }
    
  }
}



export default MainListItems;





